$(document).ready(function(){
  var options = {
  slidesToShow: 3,
  slidesToScroll: 1,
  prevArrow: false,
  nextArrow: false,
  autoplay: true,
  autoplaySpeed: 5500,

  responsive: [
    {
      breakpoint: 940,
      settings: {
        slidesToShow: 2,
        autoplaySpeed: 4500
      }
    },
    {
      breakpoint: 640,
      settings: {
        slidesToShow: 1,
        centerMode: true,
        autoplaySpeed: 3000
      }
    }
  ]
};

  $('.recomended__wrapper').slick(options);

  var filter = $('.recomended__item.active-item').data('control');
  $('.recomended__wrapper').slick('slickFilter', filter);

// Filtering slider
   $('.recomended__item').on('click', function() {

        $(this).addClass('active-item');
        $('.recomended__item').not(this).removeClass('active-item');

        var filterClass = $(this).data('control');
        $('.recomended__wrapper').slick('slickUnfilter');
        $('.recomended__wrapper').slick('unslick');
        $('.recomended__wrapper').slick(options);
        $('.recomended__wrapper').slick('slickFilter', filterClass);
   
    });

   $(".go-to").on("click",function(e){
    e.preventDefault();
    var o=$(this).attr("href");
    if($(o).length){
      var s=$(o).offset().top;
      $("body,html").stop().animate({
        scrollTop:s
      },1500)
    } else console.warn("ID don't search!")
    });

   
    //search input animation
   $('.header-controls__search').on('focus', function(){
    setTimeout(function(){
    Math.random() > .5 ? $('.header-controls__search-wrapper').css('animation', 'bord 0.35s linear forwards') : 
      $('.header-controls__search-wrapper').css('animation', 'bord2 0.35s linear forwards');
      }, 450);
    });
  

   $('.header-controls__search').on('blur', function(){
      $('.header-controls__search-wrapper').css('animation', 'none');
   });


    //mobile menu search input animation
   $('.menu__table__search').on('focus', function(){
    Math.random() > .5 ? $('.menu__table_search-wrapper').css('animation', 'bord 0.35s linear forwards') : 
      $('.menu__table_search-wrapper').css('animation', 'bord2 0.35s linear forwards');
    });
  

   $('.menu__table__search').on('blur', function(){
      $('.menu__table_search-wrapper').css('animation', 'none');
   });



   //Search input animation
   $('.header-controls__search-wrapper').on('click', function(){
      $(this).addClass('active-input');
      $('.header-controls__control').css('width', '200px').css('flex', '1');
      $(this).find('.header-controls__search').css('background-color', '#fff');
   });

   $(document).mouseup(function(e){
       var block = $(".header-controls__search-wrapper"); // определяем элемент, к которому будем применять условия (можем указывать ID, класс либо любой другой идентификатор элемента)
        if (!block.is(e.target) // проверка условия если клик был не по нашему блоку
            && block.has(e.target).length === 0) { // проверка условия если клик не по его дочерним элементам
            block.removeClass('active-input'); // если условия выполняются - скрываем наш элемент
            $('.header-controls__control').css('width', 'auto').css('flex','0 1 65%');
            block.find('input').val('');
        }
   })






//Fixed menu
   $(window).on('scroll load', function(){
      if($(this).scrollTop() > 1) {
        $('.header').addClass('fixed');
      } else {
        $('.header').removeClass('fixed');
      }
   });

//Open mobile menu
   $('.hamburger__menu-wrap').on('click', function(){
      $('.menu__layer').animate({left: '100%'}, 750, function(){
          $('.menu__table').addClass('active-table');
          $('.menu__table_social').fadeIn(800).css('display', 'flex');
         
          $('body, html').css('overflow', 'hidden');
      });
      setTimeout(function(){
          $('.menu__table').show().css('display', 'flex');
      }, 300);
      

   })
//Close mobile menu
   $('.menu__table-close-wrap').on('click', function(){
        $('.menu__table').removeClass('active-table');
        $('.menu__table_social').fadeOut(300);
        setTimeout(function(){
             $('.menu__layer').animate({left: '-200%'}, 750, function(){
          });
        }, 500)
       
        setTimeout(function(){
          $('.menu__table').hide();
          $('body, html').css('overflowX', 'hidden').css('overflowY', 'auto');
      }, 850);
   });

    $('.recomended__card-like').find('svg').each(function(){
        $(this).on('click', function(){
        $(this).css('fill', '#e00058');
        var parent = parseInt($(this).closest('.recomended__card-like').text());
        parent++
       console.log($(this).closest('.recomended__card-like')[0].childNodes[4].nodeValue = parent);
     })
   })
})






